            <div className="contact-form">
            <Heading text={"Let's get down to business"} color={'black'}/>
                    
                <div className='form-row'>
                    <div className='form-fields-column'>
                    <p className='card-description'>Looking for a professional team to handle your product? Dont forget to ping us!</p>
                    
                        <input
                        className='email-input'
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        placeholder="Enter Email"
                        />
                        <textarea
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                        placeholder="Write a message.."
                        rows="10"
                        />
                        <button className='button-style' style={{"borderRadius": "80px", "backgroundColor": "#71FA71", "width": "23vh"}}>Get a Quote</button>
                    </div>
                    <img src={contactUs}></img>
                </div>
            
            </div>

